.toolbar-div{
    position: fixed;
    -webkit-transition: all 0.5s ease-in;
    -o-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;
    left:0px;
    display: block;
    width:100%;
}
.toolbar-hide{
    bottom: -100px;
}
.toolbar-show{
    bottom: 0px;
}



